.global-nav {
  &__list {
    display: inline-flex;
    width: max-content;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: flex-end; // active stays on right
    gap: 10px;
    list-style: none;
  }

  &__item {
    // collapsed default
    width: 0;
    overflow: hidden;
    pointer-events: none;
    transform: translateX(10px);
    opacity: 0;

    transition:
      opacity 220ms ease,
      width 260ms ease,
      transform 220ms ease;

    &.is-active {
      width: auto;
      overflow: visible;
      pointer-events: auto;
      transform: translateX(0);
      opacity: 1;
    }
  }

  &:hover &__item,
  &:focus-within &__item {
    width: auto;
    overflow: visible;
    pointer-events: auto;
    transform: translateX(0);
    opacity: 1;
  }

  &__link {
    display: inline-block;
    padding: 5px 8px;
    text-decoration: none;
    white-space: nowrap;
    color: white;
    font-family: 'Silkscreen', 'arial', 'sans-serif';
    font-size: 0.9rem;
    font-weight: 400;
    transition: background 200ms ease, transform 200ms ease;

    &:hover {
    color: rgba(255,255,255,0.7);
    }
  }

  &__item.is-active &__link {
    position: relative;
    text-decoration: underline;
    text-decoration-thickness: 1px;
    text-underline-offset: 5px;
    
    &::before {
      content: "‚ùÆ"; 
      position: absolute;
      left: -10px;
      top: 55%;
      transform: translateY(-50%);
      font-size: 1rem;
      opacity: 1;
      transition: opacity 200ms ease, transform 200ms ease;
    }
  }

  &:hover &__item.is-active &__link::before,
  &:focus-within &__item.is-active &__link::before {
    opacity: 0;
    transform: translateY(-50%) translateX(-4px);
  }
}
